.page {
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  transition: opacity 500ms ease-in-out;
}

.page-enter {
  opacity: 0;
  /* transform: translateX(100%); */
}
.page-enter-active {
  opacity: 1;
  /* transform: translateX(0); */

}
.page-exit-active {
  opacity: 0;
  /* transform: translateX(-100%); */
}